import "./temp.css";

function Temp(props) {
    return (
        <div className="cont">{

(props.img && props.pink ) ?
<div>
 <img src={props.img} alt="image" className="fimg" />
 <p className="pink" >{props.pink}</p>
  </div>
 : <h3 className="title" >{props.title}</h3>}

 <ul className="un" >
     <li>filter </li>
     <li> sort</li>
 </ul>
 <div className="content" >
{props.children}
 </div>
      </div>
        
    )
}

export default Temp
